export default {
  layout: "Dum-content",
  mounted() {
    this.getfacilitie();
  },
  data() {
    return {
      facilities: [],
      facilitie: {
        title: "",
        description: "",
      },
      errors: {},
    };
  },
  methods: {
    getfacilitie() {
      this.$axios
        .$get("/facilitie/show")
        .then((res) => {
          this.facilities = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    facilitieEdit(id) {
      $("#facilitieUpdate").modal("show");
      this.$axios
        .$get("/facilitie/edit/" + id)
        .then((res) => {
          this.facilitie = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    facilitieUpdate() {
      this.$axios
        .$post("/facilitie/update/" + this.facilitie.id, this.facilitie)
        .then((res) => {
          this.getfacilitie();
          $("#facilitieUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";
        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    deletefacilitie(id) {
      if (confirm("Are you sure to delete this Facilitie?")) {
        this.$axios
          .$get("/facilitie/delete/" + id)
          .then((res) => {
            this.getfacilitie();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    facilitieStatus(id) {
      this.$axios
        .$get("/facilitie/status/" + id)
        .then((res) => {
          this.getfacilitie();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
